body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand {
  z-index: -1;
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hands;
}

.stopwatch__hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: rotate-hands;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  justify-content: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: relative;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

@keyframes rotate-hands {
  0% {
    transform-origin: bottom;
    transform: rotateZ(0);
  }

  100% {
    transform: rotateZ(360deg);
  }
}
